import React from 'react'

interface ContainerProps {
    children?: React.ReactNode
    className?: string
    shadow?: 'lg' | 'sm'
}

export const Container = ({ children, className, shadow, ...props }: ContainerProps) => {
    return (
        <div className={`container mx-auto ${className} ${shadow && `shadow-${shadow}`}`} {...props}>
            {children}
        </div>
    )
}
